import React from 'react';
import Card from './Card';

export default function Cards(props) {
  // acá va tu código
  // tip, podés usar un map
  return <div>Cards Component
    {props.map((carta)=> {
      return (<Card max={carta.main.max} min={carta.main.min} name={carta.name} img={carta.weather[0].icon} onClose={()=>{alert(carta.name)}}/>)
    })}
  </div>
};